<template>
  <div class="add-game">
    <vue-headful title="Add Game | GameBlitz" />
      <h1 class="grey--text mb-2">Add Game Title</h1>

      <v-form @submit.prevent="saveGame" ref="form">
        <v-container grid-list-md class="pa-5">
          <v-layout wrap>
            <v-flex xs12 sm6 md2>
              <v-text-field label="Game ID" v-model="game_id" prepend-icon="fas fa-id-card-alt" :rules="charIdRule"></v-text-field>
            </v-flex>
            <v-flex xs12 sm6 md10>
              <v-text-field label="Title" v-model="title" prepend-icon="person" :rules="nameRule"></v-text-field>
            </v-flex>
            <v-flex xs12 sm4 md4>
              <v-select :items="platItems" label="Platform" v-model="platform" prepend-icon="videogame_asset"></v-select>
            </v-flex>
            <v-flex xs12 sm4 md4>
              <v-text-field label="Cover" v-model="cover" prepend-icon="fas fa-image"></v-text-field>
            </v-flex>
            <v-flex xs12 sm4 md4>
              <v-text-field label="Wallpaper" v-model="bg" prepend-icon="wallpaper"></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-textarea label="Description" v-model="desc" prepend-icon="format_align_left" hint="Kindly put double br tags on white indent."></v-textarea>
            </v-flex>
            <v-flex xs12>
              <v-text-field label="Publisher" v-model="pub" prepend-icon="fas fa-user-tag"></v-text-field>
            </v-flex>
            <v-flex xs12>
              <v-text-field label="Developer" v-model="dev" prepend-icon="fas fa-laptop-code"></v-text-field>
            </v-flex>
            <v-flex xs12 sm6 md7>
              <v-text-field label="Genre" v-model="genre" prepend-icon="fas fa-tags"></v-text-field>
            </v-flex>
            <v-flex xs10 sm4 md3>
              <v-text-field label="Release Date" v-model="rel_date" prepend-icon="fas fa-calendar-week"></v-text-field>
            </v-flex>
            <v-flex xs2 sm2 md2>
              <v-switch :label="`Do you have it?: ${haveSwitch.toString()}`" v-model="haveSwitch"></v-switch>
            </v-flex>
          </v-layout>
          <!-- button -->
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn flat color="primary" type="submit" :loading="loading"><v-icon>save</v-icon>&nbsp;Create</v-btn>
            <v-btn flat color="error" to="/games"><v-icon>cancel</v-icon>&nbsp;Cancel</v-btn>
          </v-card-actions>
        </v-container>
      </v-form>
  </div>
</template>

<script>
import db from './../firebaseInit'

export default {
  name: 'add-game',
  data() {
    return {
      loading: false,
      game_id: '',
      title: '',
      rel_date: '',
      platform: '',
      genre: '',
      dev: '',
      pub: '',
      cover: '',
      bg: '',
      desc: '',
      platItems: [
        'PlayStation',
        'PlayStation 2',
        'PlayStation 3',
        'PlayStation 4',
        'Xbox',
        'Xbox 360',
        'Xbox One',
        'Nintendo Switch',
        'PC',
        'Google Stadia',
        'Mobile',
      ],
      charIdRule: [v => v.length >= 3 || 'Minimum length is 3 characters'],
      nameRule: [v => v.length >= 2 || 'Minimum length is 2  characters'],
      haveSwitch: false,
      games: [],
    }
  },
  methods: {
    saveGame() {
      if (this.$refs.form.validate()) {
        this.loading = true
        const addGame = {
          game_id: this.game_id,
          title: this.title,
          rel_date: this.rel_date,
          platform: this.platform,
          genre: this.genre,
          dev: this.dev,
          pub: this.pub,
          cover: this.cover,
          have: this.haveSwitch,
          bg: this.bg,
          desc: this.desc
        }

        db.collection('games').add(addGame).then(() => {
          this.loading = false
        })
        .then(docRef => this.$router.push('/games'))
      }
    }
  },
  created() {
    db.collection('games').orderBy('game_id', 'desc').get().then(querySnapshot => {
      querySnapshot.forEach(doc => {
        const data = {
          'id': doc.id,
          'game_id': doc.data().game_id,
          'title': doc.data().title,
          'rel_date': doc.data().rel_date,
          'platform': doc.data().platform,
          'genre': doc.data().genre,
          'dev': doc.data().dev,
          'pub': doc.data().pub,
          'cover': doc.data().cover,
          'have': doc.data().have,
          'bg': doc.data().bg,
          'desc': doc.data().desc,
        }
        this.games.push(data)
      })
    })
  },
  computed: {
    autoID() {
      this.game_id = this.games.game_id++
    }
  }
}
</script>

<style lang="sass" scoped>
</style>
